import { getAccessToken } from "./Auth";

export class Service {

    static jsonOrFail = (response) => {
        if(!response.ok){
            throw new Error(`HTTP status=${response.status}; data=${response.data}`);
        }
        return response
            .json()
            .catch((err) => {
                throw new Error(err);
            })
    }

    static getRequest = async (endpoint, auth=true) =>{
        const actualToken = getAccessToken();
        if(!actualToken && auth){
            return new Promise((resolve, reject) => {
                reject('No auth-token found.');
            });
        };
        const authHeader = auth && {Authorization: `Bearer ${actualToken}`} || {}
        return fetch(`${process.env.REACT_APP_BACKEND_URL}/v1${endpoint}`, {
            headers: {
                "Content-Type": "application/json",
                ...authHeader,
            },
            method: "GET",
        }).then(Service.jsonOrFail);
    }

    static postRequest = async (endpoint, data, auth=true) =>{
        const actualToken = getAccessToken();
        if(!actualToken && auth){
            return new Promise((resolve, reject) => {
                reject('No auth-token found.');
            });
        };
        const authHeader = auth && {Authorization: `Bearer ${actualToken}`} || {}
        return fetch(`${process.env.REACT_APP_BACKEND_URL}/v1${endpoint}`, {
            headers: {
                "Content-Type": "application/json",
                ...authHeader,
            },
            method: "POST",
            body: JSON.stringify(data),
        }).then(Service.jsonOrFail);
    }

    static getAbi = async (abiName) => {
        const endpoint = `/abi/${abiName}`;
        return Service.getRequest(endpoint);
    }

    static txnPlayerGameTicker = () => {
        const endpoint = `/txn/player/game-ticket`;
        return Service.postRequest(endpoint, {});
    }

    static userCreateAccessToken = (publicAddress, signature) => {
        const endpoint = `/auth/accesstoken`;
        return Service.postRequest(endpoint, {signature, publicAddress}, false);
    }

    static userGetNonce = (publicAddress) => {
        const endpoint = `/auth/nonce/${publicAddress}`;
        return Service.getRequest(endpoint, false);
    }

    static getWalletBalance = () => {
        const endpoint = `/wallet`;
        return Service.getRequest(endpoint);
    }

    static getAbiFutureToken = () => {
        const endpoint = `/abi/futuretoken`;
        return Service.getRequest(endpoint);
    }

    static getConsumableBalance = async () => {
        const endpoint = `/txn/player/game-ticket`;
        return Service.getRequest(endpoint);
    };

    static consumeGameTicket = async () => {
        const endpoint = `/txn/player/execute-gameplay`;
        return Service.postRequest(endpoint, { x: "1", y: "1" });
    };

    static getTransactions = async () => {
        const endpoint = `/txn`;
        return Service.getRequest(endpoint);
    };

}
