import Web3 from "web3";
import {Service} from "./Service";

export class Web3Service{

    static instance = undefined;

    static getInstance = async () => {
        console.log("Web3Service.getInstance")
        if(Web3Service.instance){
            console.log("Web3Service.getInstance from cache")
            return Web3Service.instance ;
        }
        Web3Service.instance = new Web3Service();
        await Web3Service.instance.init();
        return Web3Service.instance;
    }

    web3 = undefined;
    futureTokenABI = undefined;
    contractAddress = undefined;
    init = async () => {
        if(this.web3){
            console.log("Web3 from cache.")
            return this.web3;
        }

        if (!window.ethereum) {
            window.alert("Please install MetaMask first.");
            return;
        }

        if (!this.web3) {
            try {
                // Request account access if needed
                await window.ethereum.enable();

                // We don't know window.web3 version, so we use our own instance of Web3
                // with the injected provider given by MetaMask
                this.web3 = new Web3(window.ethereum);
            } catch (error) {
                this.window.alert("You need to allow MetaMask.");
                return;
            }
        }

        this.futureTokenABI = await Service.getAbiFutureToken();
        const futureABI = this.futureTokenABI;
        // const futureRopsten = futureABI.networks[3];
        // this.contractAddress = futureRopsten.address;
        this.contractAddress = process.env.REACT_APP_TOKEN_ADDRESS; 
        this.futureToken = new this.web3.eth.Contract(futureABI.abi, this.contractAddress);
    }

    userAddress = async() => {
        const coinbase = await this.web3.eth.getCoinbase();
        if (!coinbase) {
            window.alert("Please activate MetaMask first.");
            return;
        }
        return coinbase.toLowerCase();
    }

    // Popup MetaMask confirmation modal to sign message
    loginSignature = async(publicAddress, nonce) => {
        const coinbase = await this.web3.eth.getCoinbase();
        if (!coinbase) {
            window.alert("Please activate MetaMask first.");
            return;
        }
        const loginMessage = `I am signing my one-time nonce: ${nonce}`;
        try {
            const signature = await this.web3.eth.personal.sign(
                loginMessage,
                publicAddress,
                "" // MetaMask will ignore the password argument here
            );
            return signature;
        } catch (err) {
            console.log(err);
            throw new Error("You need to sign the message to be able to log in.");
        }
    }

    balanceOf = async (userAddress) => {
        const dappTokenBalance = await this.futureToken.methods.balanceOf(userAddress).call()
        return dappTokenBalance.toString();
    }

    transfer = async (senderAddress, recevierAddress, amount) => {
        await this.futureToken.methods.transfer(recevierAddress, amount).send({ from: senderAddress });
    }

    payGameFee = async (amount) => {
        const userAddress = await this.userAddress();
        const gameAddress = await this.futureToken.methods.gameAddress().call();
        await this.futureToken.methods.transfer(gameAddress, amount).send({ from: userAddress });
    }

    // TODO: fix txnPlayerToGame contract and re-check
    // BETA: need testing
    payGameFeeNative = async (userAddress, amount) => {
        await this.futureToken.methods.txnPlayerToGame(amount).send({ from: userAddress });
    }

    addTokenToWallet = async () => {
        const tokenAddress = this.contractAddress;
        const tokenSymbol = 'KHO';
        const tokenDecimals = 18;

        try {
            // wasAdded is a boolean. Like any RPC method, an error may be thrown.
            const wasAdded = await window.ethereum.request({
                method: 'wallet_watchAsset',
                params: {
                type: 'ERC20', // Initially only supports ERC20, but eventually more!
                options: {
                    address: tokenAddress, // The address that the token is at.
                    symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
                    decimals: tokenDecimals, // The number of decimals in the token
                    //image: tokenImage, // A string url of the token logo
                },
                },
            });
        } catch (error) {
            console.log(error);
        }
    }
}
